import Vue from 'vue';
import Vuex from 'vuex';

import app from './modules/app';
import errorLog from './modules/errorLog';
import getters from './getters';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    app,
    errorLog,
  },
  getters,
});



